body {
    @include font-feature-settings("kern", "liga", "pnum");
    -webkit-font-smoothing: antialiased;
    color: $base-font-color;
    font-family: $base-font-family;
    font-size: $base-font-size;
    line-height: $base-line-height;
}

p {
    margin: 0 0 $base-gap;
}

a {
    color: $base-font-color;
    text-decoration: none;

    &:active,
    &:focus {
       outline: none;
    }
}

h1, h2, h3, h4, h5, h6 {
    font-family: $heading-font-family;
    line-height: $heading-line-height;
    color: $heading-font-color;
}

%h1 {
    font-size: 2.5em;
    text-align: center;
    &:after {
        content: "";
        display: block;
        height: 1px;
        margin: $base-gap auto;
        width: 50%;
        background-color: $base-border-color;
    }
}

%h2 {
    font-size: 1.6em;
    margin-top: $base-gap;
    margin-bottom: $base-gap * 0.9;
} 

%h3 {
    font-size: 1.35em;
    margin-top: $base-gap * 1.25;
    margin-bottom: $base-gap * 0.5;
} 

%h4,
%h5,
%h6 {
    font-size: 1.1em;
    margin-top: $base-gap * 1.25;
    margin-bottom: $base-gap * 0.125;
}

%a {
    transition: color 0.1s linear;
    font-weight: bold;
    border-bottom: 1px dotted $base-font-color;

    &:active,
    &:focus,
    &:hover {
        color: $action-color;
    }
}

#{$text-containers} {
    font-family: $text-font-family;

    h1 { 
        @extend %h1;
        margin-top: $base-gap * 1.5;
    }
    h2 { @extend %h2; }
    h3 { @extend %h3; }
    h4, h5, h6 { @extend %h4; } 

    a { @extend %a; }

    > p:last-child {
        margin-bottom: 0;
    }
}

h1.entry-title {
    @extend %h1;
}

h2.entry-title {
    @extend %h3;
    margin-top: 0;
    margin-bottom: $base-gap / 2
}

.entry-title a {
    @extend %a;
    border-bottom: none;
}


.widget-title {}
.archive-title {}
