//* Content Container
#main #content {
	background-color: rgba( $content-background-color, 0.5 );
}



//* Archives
.plural {
	article.entry {
		// padding-bottom: $base-gap;
		border-bottom: 1px dashed $base-border-color;
		&:last-child { 
			border-bottom: none; 
		}

		.entry-header {}
			.entry-details {
				// margin-bottom: $base-gap / 10;
				font-size: 0.8em;
			}
				.category a {
					color: $meta-font-color;
				}

				time {
					white-space: nowrap;
				}

			.entry-title {}

		.entry-content {}		
	}
}
